import { BaseContainerConfig } from './BaseContainerConfig';

export class BaseTableConfig extends BaseContainerConfig {
    public topleftShowTableName: boolean = false; // 
    public tableName: string = ''; // 
    public debugLevel?: number = 0; // 1 0
    public selectBoxId?: string; // focus
    public tableGroupId?: string;
    public isShowColIndex?: boolean = true;  //  ABDC
    public isShowRowIndex?: boolean = true; //  1234
    public isOverflow?: boolean = false; // 
    public canResizeWidth?: boolean = false; //  
    public hasTopFixedTable?: boolean = true; // 
    public hasLeftFixedTable?: boolean = true; // 
    public hasTopThead?: boolean = true; // 
    public hasLeftThead?: boolean = true; // 
    public renderEvent?: string[] = ['blur', 'change', 'paste'];
    public maxWidth?: string = '100%'; //  
    public maxHeight?: string = '200px'; //  
}
